.loaderWrapper {
    position: fixed;
    width: 100vw !important;
    height: 100vh !important;
    background: #171717;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: fadeLoader 1s 3s ease forwards;
  }
  
  .loader {
    display: flex;
    z-index: 999;
    position: absolute;
    width: 100%;
    justify-content: center;
    flex-flow: column-reverse;
  }
  .loader .img {
      width: auto;
    height: 35px;
    margin: 2px;
    animation: img 1s infinite linear;
  }
  
  .loader img {
    animation-delay: 0s;
    height: 45px;
  }
  /* .loader .bar:nth-child(2) {
    animation-delay: 0.25s;
  }
  .loader .bar:nth-child(3) {
    animation-delay: 0.5s;
  } */
  
  @keyframes img {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(0.95);
    }
    100% {
      transform: scale(1);
    }
  }
  
  @keyframes fadeLoader {
    to {
      opacity: 0;
      z-index: -1;
    }
  }
  
